<template>
  <div id="app">
    <div class="row">
      <div class="col-md-12">
        <div class="form-group">
          <label>قم برفع فيديو</label>
          <input ref="file" @change="getVideo" type="file" class="form-control" />
        </div>
      </div>
    </div>
    <!-- PROGRESS -->
    <div v-if="percentage > 0" class="progress">
      <div class="progressbar" :style="{ width: percentage + '%' }">
        {{ percentage }}%
      </div>
    </div>
    <div class="row justify-content-center">
      <div class="col-md-4">
        <button
          :disabled="!uploadBtnEnabled"
          @click="upload"
          class="btn btn-primary w-100"
        >
          Upload
        </button>
      </div>
      <div v-if="resumeBtnEnabled || pauseBtnEnabled" class="col-md-4">
        <button
          v-if="resumeBtnEnabled"
          @click="resumePause"
          class="btn btn-success w-100"
        >
          resume
        </button>
        <button
          v-if="pauseBtnEnabled"
          @click="resumePause"
          class="btn btn-secondary w-100"
        >
          pause
        </button>
      </div>
      <div class="col-md-4">
        <button :disabled="!cancelBtnEnabled" @click="reset" class="btn btn-danger w-100">
          Cancel
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { url } from "../../main";
import Axios from "axios";
export default {
  name: "stream-upload-video",
  props: ["fileName", "lessonData"],
  created() {
    // if (localStorage.getItem("chunks")) {
    //   this.chunks = JSON.parse(localStorage.getItem("chunks"));
    //   this.upload();
    // }
  },
  data() {
    return {
      file: null,
      chunks: [],
      uploaded: 0,
      startUpdate: false,
      resume: true,
    };
  },
  watch: {
    formData(val) {},
  },
  filters: {},
  watch: {
    chunks(val) {
      if (val.length > 0 && this.startUpdate) {
        this.upload();
        localStorage.setItem("chunks", JSON.stringify(this.chunks));
      }
    },
  },
  computed: {
    percentage() {
      if (!this.file) return 0;
      return ((this.uploaded * 100) / this.file.size).toFixed(1);
    },
    uploadBtnEnabled() {
      return this.chunks.length > 0 && this.uploaded == 0;
    },
    resumeBtnEnabled() {
      return this.chunks.length > 0 && !this.resume && this.uploaded > 0;
    },
    pauseBtnEnabled() {
      return this.chunks.length > 0 && this.resume && this.uploaded > 0;
    },
    cancelBtnEnabled() {
      return this.chunks.length > 0 && this.uploaded > 0;
    },
  },
  methods: {
    getVideo(e) {
      const file = e.target.files[0];
      const type = file.type.split("/")[1];
      if (!["mp4", "wav"].includes(type)) return alert("صيغة الفيديو غير صحيحة");
      if (file.size > 10 ** 9) return alert("حجم الملف تجاوز الحجم المسموح به");
      this.file = file;
      this.createChunks();
    },
    upload() {
      if (this.chunks.length == 0) return;
      if (!this.lessonData.name || !this.lessonData.section || !this.lessonData.stage) {
        return alert("كمل بقيت البيانات وأرفع الفيديو");
      }
      if (!this.resume) return;
      this.startUpdate = true;
      const formData = new FormData();
      formData.set("isLast", this.chunks.length == 1);
      formData.set("fileName", this.fileName);
      formData.set("chunk", this.chunks[0], this.file.name + ".chunk");
      const config = {
        method: "POST",
        url: "/uploadVideoByChunks",
        data: formData,
        headers: { "Content-Type": "application/octet-stream" },
        onUploadProgress: (e) => {
          this.uploaded += e.loaded;
        },
      };
      Axios(config)
        .then((res) => {
          this.chunks.shift();
          if (res.data.done) {
            this.$emit("uploaded", res.data.videoFile);
            this.reset();
          }
        })
        .catch((e) => console.log(e));
    },
    createChunks() {
      const size = this.file.size;
      const chunkSize = 10 * 10 ** 5;
      const chunksLen = Math.ceil(size / chunkSize);
      for (let i = 0; i < chunksLen; i++) {
        const slice = this.file.slice(
          i * chunkSize,
          Math.min(i * chunkSize + chunkSize, this.file.size),
          this.file.type
        );
        this.chunks.push(slice);
      }
    },
    resumePause() {
      this.resume = !this.resume;
      if (this.resume) this.upload();
    },
    reset() {
      this.chunks = [];
      this.file = null;
      this.uploaded = 0;
      localStorage.removeItem("chunks");
      this.resume = true;
      this.startUpdate = false;
      this.$refs.file.value = "";
    },
  },
};
</script>

